<template>
	<div style="display: flex; flex-direction: column; align-items: center;">
		<span :class="{'txt-points': true}">{{ points }}</span>
		<span 
			v-if="animateDecrease || animateIncrease" 
			:class="{'txt-point-decrese': true, 
			'fade-out': animateFadeOut, 
			'fade-in': animateFadeIn}">
			
			{{ decreaseAmount }}

		</span>
	</div>
</template>

<script>
export default {
  name: 'PointsComponent',
  props: {
    points: {
      type: Number,
      required: true
    }
  },
  data() {
    return {
      previousPoints: this.points,
      animateDecrease: false,
      animateFadeOut: false,
			decreaseAmount: 0,
			animateIncrease: false,
      animateFadeIn: false,
    };
  },
  watch: {
    points(newVal, oldVal) {
      if (newVal > oldVal) {
        this.onPointsIncrease(newVal - oldVal);
      } else if (newVal < oldVal) {
        this.onPointsDecrease(oldVal - newVal);
      }
      this.previousPoints = newVal;
    }
  },
  methods: {
    onPointsIncrease(increaseAmount) {
      console.log(`Points increased by ${increaseAmount}`);
      this.animateIncrease = true;
      this.animateFadeIn = true;
			this.decreaseAmount = "+"+increaseAmount;

      setTimeout(() => {
        this.animateIncrease = false;
        this.animateFadeIn = false;
      }, 1000);  
    },
    onPointsDecrease(decreaseAmount) {
      console.log(`Points decreased by ${decreaseAmount}`);
      this.animateDecrease = true;
      this.animateFadeOut = true;
			this.decreaseAmount = "-"+decreaseAmount;

      setTimeout(() => {
        this.animateDecrease = false;
        this.animateFadeOut = false;
      }, 1000);  
    }
  }
};
</script>
<style scoped>
.txt-points{
  margin-left: 8px;
  font-size: 22px;
  font-weight: 800;
  transition: font-size 1s, color 0.5s;
}

@keyframes fadeOut {
  0% { opacity: 1; transform: translateY(0); }
  100% { opacity: 0; transform: translateY(30px); }
}

.fade-out {
  animation: fadeOut 1s forwards;
	color: #d36969;
}
.txt-point-decrese{
  position: absolute;
  margin-top: 20px;
  font-size: 20px;
  font-weight: 800;
}

@keyframes fadeIn {
  0% { opacity: 0; transform: translateY(30px); }
  100% { opacity: 1; transform: translateY(0); }
}

.fade-in {
  animation: fadeIn 1s forwards;
	color: #3AA394;
}
</style>
