<template>
  <div>
    <nav style="display: flex; align-items: center; justify-content: space-between; max-height: 80px; padding: 0 10px;">
      <div style="display: flex; align-items: center;">
        <img style="width: 1.8rem;" src="@/assets/coin.png"/>
        <Points :points="points"/>
      </div>
      <button class="btn" @click="showModal = true"><i class="fas fa-cog"></i></button>
    </nav>

    <div class="word" :class="{ 'word-effect': someCondition }">
      <div v-for="(letter, index) in wordArray" :key="index" class="block">
        <span class="letter">{{ guessedLetters.includes(letter) ? letter : ' __ ' }}</span>
      </div>
    </div>
    <div style="text-align: center;">
      <span>{{ error }}</span>


      <button v-if="someCondition" class="btn btn-primary" type="button" @click="play()">Jogar Novamente</button>
    </div>
    
    

    <div v-if="showModal" class="modal">
        <div class="modal-content">
            <span @click="showModal = false" class="close">&times;</span>
            
            <p tabindex="0" class="help_multi">Encontre as palavras corretas. Após cada tentativa, as peças indicam o quão próximo você está da solução.</p>
            <p>Selecione abaixo o número de letras da palavra:</p>

            <div>
              <button 
                  v-for="size in wordSizes" 
                  :key="size" 
                  @click="selectWordSize(size)" 
                  :class="['btn', 'mr-1', { 'btn-selected': selectedWordSize === size }]">
                  {{ size }}
              </button>
            </div>

            <p>Selecione a dificuldade: </p>

            <button 
                  v-for="size in difficulty" 
                  :key="size.dif" 
                  @click="selectNivel(size.nivel)" 
                  :class="['btn', 'mr-1', { 'btn-selected': nivel === size.nivel }]">
                  {{ size.nivel }}
              </button>

            <p tabindex="0">Os acentos são preenchidos automaticamente e não são considerados nas dicas.</p>
            <p tabindex="0">As palavras podem conter letras repetidas.</p>
            
            <div>
              <button @click="playModal()" class="btn" style="width: 100%">Jogar agora</button> 
            </div>
        </div>
    </div>

    
    <div class="keyboard fixed-footer">

      <div v-if="someCondition" class="above-keyboard">
        <p>Inicie o jogo novamente.</p>
      </div>
      <!-- :class="buttonClass(letter)" @click="markAsUsed(letter)" -->
        <div class="teclas">
          <div>
            <button :class="buttonClass('Q')" @click="markAsUsed('Q')" type="button" class="keyboard-btn">Q</button>
            <button :class="buttonClass('W')" @click="markAsUsed('W')" type="button" class="keyboard-btn">W</button>
            <button :class="buttonClass('E')" @click="markAsUsed('E')" type="button" class="keyboard-btn">E</button>
            <button :class="buttonClass('R')" @click="markAsUsed('R')" type="button" class="keyboard-btn">R</button>
            <button :class="buttonClass('T')" @click="markAsUsed('T')" type="button" class="keyboard-btn">T</button>
            <button :class="buttonClass('Y')" @click="markAsUsed('Y')" type="button" class="keyboard-btn">Y</button>
            <button :class="buttonClass('U')" @click="markAsUsed('U')" type="button" class="keyboard-btn">U</button>
            <button :class="buttonClass('I')" @click="markAsUsed('I')" type="button" class="keyboard-btn">I</button>
            <button :class="buttonClass('O')" @click="markAsUsed('O')" type="button" class="keyboard-btn">O</button>
            <button :class="buttonClass('P')" @click="markAsUsed('P')" type="button" class="keyboard-btn">P</button>
          </div>
          <div>
            <button :class="buttonClass('A')" @click="markAsUsed('A')" type="button" class="keyboard-btn">A</button>
            <button :class="buttonClass('S')" @click="markAsUsed('S')" type="button" class="keyboard-btn">S</button>
            <button :class="buttonClass('D')" @click="markAsUsed('D')" type="button" class="keyboard-btn">D</button>
            <button :class="buttonClass('F')" @click="markAsUsed('F')" type="button" class="keyboard-btn">F</button>
            <button :class="buttonClass('G')" @click="markAsUsed('G')" type="button" class="keyboard-btn">G</button>
            <button :class="buttonClass('H')" @click="markAsUsed('H')" type="button" class="keyboard-btn">H</button>
            <button :class="buttonClass('J')" @click="markAsUsed('J')" type="button" class="keyboard-btn">J</button>
            <button :class="buttonClass('K')" @click="markAsUsed('K')" type="button" class="keyboard-btn">K</button>
            <button :class="buttonClass('L')" @click="markAsUsed('L')" type="button" class="keyboard-btn">L</button>
          </div>
          <div>
            <button :class="buttonClass('Z')" @click="markAsUsed('Z')" type="button" class="keyboard-btn">Z</button>
            <button :class="buttonClass('X')" @click="markAsUsed('X')" type="button" class="keyboard-btn">X</button>
            <button :class="buttonClass('C')" @click="markAsUsed('C')" type="button" class="keyboard-btn">C</button>
            <button :class="buttonClass('V')" @click="markAsUsed('V')" type="button" class="keyboard-btn">V</button>
            <button :class="buttonClass('B')" @click="markAsUsed('B')" type="button" class="keyboard-btn">B</button>
            <button :class="buttonClass('N')" @click="markAsUsed('N')" type="button" class="keyboard-btn">N</button>
            <button :class="buttonClass('M')" @click="markAsUsed('M')" type="button" class="keyboard-btn">M</button>
            <button :class="buttonClass('ENTER')" @click="markAsUsed('ENTER')" type="button" class="keyboard-btn key-enter">ENTER</button>
          </div>
        </div>
    </div>
  </div>
</template>


<script>
import axios from "axios";
import Points from "@/components/Points"

export default {
  name: 'App',
  components: {
    Points,
  },
  data() {
    return {
      word: '',
      wordNormalized: '',
      usedButtons: [],
      guessedLetters: [],
      wrongLetters: [],
      someCondition: false,
      selectedWordSize: "6",
      showModal: true,
      wordSizes: ["3-", "4", "5", "6", "7", "8", "9+", '*'],
      nivel: 3,
      difficulty: [{
        nivel: 1,
        dif: "Muito fácil",
        file: "very_easy.json"
      },
      {
        nivel: 2,
        dif: "Fácil",
        file: "easy.json"
      },{
        nivel: 3,
        dif: "Média",
        file: "medium.json"
      },{
        nivel: 4,
        dif: "Difícil",
        file: "hard.json"
      },
      {
        nivel: 5,
        dif: "Muito Difícil",
        file: "very_hard.json"
      }],
      error: '',
      points: 100,
      animateDecrease: false,
      animateFadeOut: false,
    };
  },
  computed: {
    wordArray() {
      return this.wordNormalized.split('');
    },
  },
  methods: {
    getFileNameByLevel(nivel) {
        // Procurar o objeto correspondente ao nível fornecido
        const result = this.difficulty.find(item => item.nivel === nivel);

        // Verificar se o nível foi encontrado e retornar o nome do arquivo, caso contrário, retornar null
        return result ? result.file : null;
    },
    async listWordsJson(file) {
      try {
          // Utilize a URL completa para a requisição GET
          const response = await axios.get(`words/${file}`); // Assumindo que 'words' é o caminho correto para os arquivos

          // Retorne diretamente response.data, pois já se espera que seja um objeto com uma propriedade 'words'
          return response.data;
      } catch (error) {
          console.error('Erro ao buscar palavras:', error);
          return 'Erro ao buscar palavras.';
      }
    },
    async playModal(){
      await this.play();
      this.showModal = false;
    },
    async play(){
      this.refresh();
      var numLetras = this.selectedWordSize;
      var file = this.getFileNameByLevel(this.nivel);
      var randomWord = await this.chooseRandomWord(numLetras, file);
      console.log(randomWord);
      if(randomWord){
        this.wordNormalized = this.removerAcentos(randomWord.toUpperCase());
      } else{
        this.error = "Nenhuma palavra encontra com as configurações especificadas."
      }            
    },
    selectWordSize(size){
      this.selectedWordSize = size;
    },
    selectNivel(nivel){
      this.nivel = nivel;
    },
    countUniqueLetters(str) {
      // Converter a string para minúsculas para tratamento de caso insensível
      str = str.toLowerCase();
      
      // Objeto para armazenar letras únicas
      const uniqueLetters = {};
      
      // Iterar pelos caracteres da string
      for (let i = 0; i < str.length; i++) {
        const char = str[i];
        // Verificar se o caractere é uma letra do alfabeto (a-z)
        if (/[a-z]/.test(char)) {
          // Adicionar ao objeto se ainda não estiver presente
          if (!uniqueLetters[char]) {
            uniqueLetters[char] = true;
          }
        }
      }
      return Object.keys(uniqueLetters).length;
    },
    refresh(){
      this.word = '';
      this.wordNormalized = '';
      this.usedButtons = [];
      this.guessedLetters = [];
      this.wrongLetters = [];
      this.someCondition = false;
      this.error = '';
      //this.wordNormalized = this.removerAcentos(this.chooseRandomWord().toUpperCase());
    },
    removerAcentos(palavra) {
        return palavra.normalize("NFD").replace(/[\u0300-\u036f]/g, "");
    },
    markAsUsed(letter) {
      if (!this.usedButtons.includes(letter)) {
        this.usedButtons.push(letter);
        if (this.wordNormalized.toUpperCase().includes(letter)) {
          this.guessedLetters.push(letter);          
        } else {
          this.wrongLetters.push(letter);

         this.decreasePoints();
        }

        if(this.countUniqueLetters(this.wordNormalized) == this.guessedLetters.length){
          this.someCondition = true;
          this.increasePoints();
        }
      }
    },
    buttonClass(letter) {
      if (this.guessedLetters.includes(letter)) {
        return 'btn-correct';
      } else if (this.wrongLetters.includes(letter)) {
        return 'btn-wrong';
      } else {
        return '';
      }
    },
    async chooseRandomWord(numLetras, file) {
      try {
        const words = await this.listWordsJson(file);
        
        // Definir o filtro de acordo com numLetras
        const filterMap = {
          "3-": palavra => palavra.length <= 3,
          "4": palavra => palavra.length === 4,
          "5": palavra => palavra.length === 5,
          "6": palavra => palavra.length === 6,
          "7": palavra => palavra.length === 7,
          "8": palavra => palavra.length === 8,
          "9+": palavra => palavra.length >= 9
        };

        const filterFunction = filterMap[numLetras] || (() => true);
        const wordsFilter = words.filter(filterFunction);

        // Verifica se há palavras encontradas
        if (wordsFilter.length === 0) {
          return false;
        }

        // Escolhe uma palavra aleatoriamente da lista filtrada
        const indice = Math.floor(Math.random() * wordsFilter.length);
        return wordsFilter[indice];
      } catch (err) {
        console.error("Erro ao escolher palavra:", err);
        return "";
      }
    },
    decreasePoints() {
      this.points = Math.max(0, this.points - 10); 
      if(this.points == 0){
        this.someCondition = true;
      }     
    },
    increasePoints() {
      this.points = Math.max(0, this.points + 50);      
    }
  },
};
</script>

